import "./index.scss"

export const Buy = () => {
    return (
        <div className="buy">
            <div className="title"> :</div>
            <div className="partners">
                {
                    Array(6).fill(0).map((_, i) => (
                        <div className="partner"> {i+1} </div>
                    ))
                }
            </div>
        </div>
    )
}